

.Nav{
    width: 100%;
    height: 100px;
    position: fixed;
    z-index: 2;
    font-family: "firagoRegular";
    font-feature-settings: "case";
}

.navbar-upper{
    background-color: #103D34;
    width: 100%;
    height: 40%;
    display: flex;
    box-sizing: content-box;
    align-items: center;
    justify-content: center;
    gap: 50px;
    font-size: 16px;
    font-weight: 500;
}

.navbar-upper span{
    color: #C2A561;
}

.navbar-bottom{
    background-color: #103d3465;
    height: 60%;
    width: 100%;
}